.popup__close-button {
  width: 32px;
  height: 32px;
  background: transparent url("../../../images/Close-Icon.svg") center no-repeat;
  background-size: 32px 32px;
  position: absolute;
  top: -40px;
  right: -40px;
  border: 0;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.popup__close-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 879px) {
  .popup__close-button {
    background-size: 20px 20px;
    width: 20px;
    height: 20px;
    right: 0;
  }
}
